

   .ServiceCard-container{
	width: 22%;
	height: 500px;
	background-color: white;
	margin: 4%;
	border-radius: 25px;
	display: flex;
	flex-direction: column;
	align-items: center;
	
-webkit-box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
-moz-box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
}
.ServiceCard-img{
	width: 100%;
	border-top-left-radius: 25px;
	border-top-right-radius:25px;

}
.ServiceCard-title{
	font-size: 1.8rem;
	margin: 0;
	
}
.ServiceCard-text{
	font-size: 1.2rem;
	text-align: center;
}

@media (max-width: 1200px) {
.ServiceCard-container{
width: 40%;
height: 100%;

}
}

@media (max-width: 700px) {

.ServiceCard-container{
width: 80%;
height: 99%;

}
.ServiceCard-title{
	width: 82%;
	
}
}