   @keyframes fadeEffect {
   	0%{opacity: 0; transform: scale3d(.75,.75,1);}
   	70%{opacity: 0; transform: scale3d(.75,.75,1);}
   	100%{opacity: 1; transform: scale3d(1,1,1); }
   }
.Services-Container{
	width: 100%;
	height: 100%;
    display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	background: rgb(255,218,31);
background: linear-gradient(180deg, rgba(255,218,31,1) 0%, rgba(255,233,123,1) 48%, rgba(255,239,161,1) 100%);
margin: 0;
border-bottom:  1px solid black;
}
.Services-img{
	width: 10%;
}
.Services-title{
	font-size: 4rem;
	font-family: 'Jost', sans-serif;
	font-weight:800;
	margin: 0;
}
.Services-cardcontainer{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}


@media (max-width: 700px) {
.Services-title{
	font-size: 2.5rem;
animation: fadeEffect 2s ;
text-align: center;
}
.Services-img{
	width: 30%;
	animation: fadeEffect 1.5s ;
}
.Services-cardcontainer{
.Services-cardcontainer{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
}

}