.Form-container{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.Form-container-title{
font-family: 'Jost', sans-serif;
font-weight:500;
font-size: 2rem;
}
.Form-container-Form{
width: 37%;
height: 600px;
border:  1px solid black;
border-radius: 25px;
background: #f4511e;
margin-bottom: 5rem;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
font-family: 'Jost', sans-serif;
font-weight:500;
font-size: 1.2rem;
-webkit-box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
-moz-box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
}
.Form-email-input{
	width: 50%;
	height: 20px;
	margin: 10px;
}
.Form-message-input{
	width: 50%;
	height: 150px;
	margin: 10px;
}
.Form-button-input{
	width: 200px;
	height: 50px;
	background-color: white;
	color: black;
	border-radius: 25px;
	margin: 10px;
	 font-family: 'Jost', sans-serif;
    font-weight:500;
    font-size: 1.2rem;
}



@media (max-width: 1200px) {
.Form-container-Form{
	width: 80%;
	height: 450px;
  margin-bottom: 1rem;
}
}




@media (max-width: 700px) {
.Form-container-title{
font-size: 0.7rem;
}

.Form-container-Form{
width: 90%;
height: 450px;
margin-bottom: 1rem;
}
.Form-email-input{
	width: 90%;
}
.Form-message-input{
	width: 90%;
}
.Form-container-response{
	font-size: 1.5rem;
}
}