.About-main{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.About-photocontainer{
	display: flex;
	justify-content: center;
	align-items: center;
}
.About-img{
width: 80%;
-webkit-box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
-moz-box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
box-shadow: 3px 2px 10px 1px rgba(82,82,82,0.67);
	
}
.About-title{

z-index: 10;
color: white;
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -80%);
font-family: 'Jost', sans-serif,800;
font-size: 5.5rem;
text-align: center;
}
.About-desctiption{
width: 80%;
display: flex;
flex-direction: column;
justify-content: center;
font-family: 'Jost', sans-serif,800;
font-size: 1.5rem;
text-align: center;
}


@media (max-width: 1000px) {
.About-title{

	font-size: 4rem;
	top: 40%;
    left: 50%;
    transform: translate(-50%, -80%);

}
}
@media (max-width: 600px) {

.About-img{
width: 100%;

}
.About-title{
z-index: 10;
color: white;
position: absolute;
top: 20%;
left: 50%;
transform: translate(-50%, -80%);
font-family: 'Jost', sans-serif,800;
font-size: 2rem;
}
.About-desctiption{
width: 93%;
font-size: 1.2rem;

}
}