.Links{ display: flex; 

}
.nav-main-container{ width: 100%; 
display: flex;
justify-content: center;
align-items: center;
}
.nav-links{ 
	display: flex;
	list-style: none;
	width: 500px;
	height: 100%;
	justify-content: space-between;
	align-items: center;

	
	 }
.nav-li-a{
	width: 100%;
	text-decoration: none;
	font-size: 1.1rem; 
    font-family: 'Jost', sans-serif;
	font-weight:500;
color: black;

}


@media (max-width: 600px) {
.nav-main-container{
	width: 100%; 


}
.nav-links{
	position: absolute;
	background-color: #ffda1f;
	height: 35vh;
	width: 100%;
	flex-direction: column;
    left: 50%;
    transform: translateX(-50%);
    padding-bottom: 5px;
    margin: 0px; 
    z-index: 100;
border-bottom: 2px solid black;}
.Linksclose{
	display: none;
	z-index: 100;

}
}
