   @keyframes fadeEffect {
   	0%{opacity: 0; transform: scale3d(.75,.75,1);}
   	70%{opacity: 0; transform: scale3d(.75,.75,1);}
   	100%{opacity: 1; transform: scale3d(1,1,1); }
   }


   .slide-container{
	
	position: relative;
	margin-bottom:  -4px;
	padding: 0;

}
.slide-img{
	width: 100%;


}
.slide-text{
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
font-family: 'Jost', sans-serif,800;
font-size: 1.8rem;
letter-spacing: 2px;
color: white;
z-index: 3;
text-align: center;

}

.slide-button{
	position: absolute;
   top: 10%;
   right: 10%;
animation: fadeEffect 3s ;
}
.slide-button-style{
	width: 200px;
	height: 50px;
	border: none;
    border-radius: 25px;
    background-color: #f4511e;
    color: white;
    font-size: 1.2rem;
}
@media (max-width: 1200px) {
		.slide-text{
font-size: 1.2rem;
letter-spacing: 1px;
color: white;
z-index: 3;
width: 80%;


}
}




@media (max-width: 700px) {
	.slide-text{

font-size: 0.8rem;
letter-spacing: 1px;
color: white;
z-index: 3;
width: 93%;


}

.slide-button{
display: none;
   
}
}