

   @keyframes fadeEffect {
   	0%{opacity: 0; transform: scale3d(.75,.75,1);}
   	70%{opacity: 0; transform: scale3d(.75,.75,1);}
   	100%{opacity: 1; transform: scale3d(1,1,1); }
   }
   

   .Top-container{
	width: 100%;
	height: 90px;
    display: flex;
	justify-content: space-around;
	align-items: center;
}
.Top-img{
	height: 100%;
	animation: fadeEffect 1s ;
}
.Top-Text{
	font-size: 1.2rem;
	font-family: 'Jost', sans-serif;
	font-weight:300;
	animation: fadeEffect 2s ;
}

@media (max-width: 600px) {
.Top-container{
display: none;
	
}
}





